.element-hidden,
.element-invisible {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  border: 0 !important;
}


.group::after {
  content: '';
  clear: both;
  display: block;
  font-size: 0;
  height: 0;
  line-height: 0;
  visibility: hidden;
}

.proto-sitemap-body {
  background-color: #eeeff4;
  padding: 50px 0;
}

.proto-sitemap-header {
  background-color: #000;
}

.proto-sitemap-header .container {
  display: flex;
  justify-content: center;
}

.proto-sitemap-links-cont {
  display: flex;
  flex-wrap: wrap;
}

.proto-sitemap-link {
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 97%;
  height: 60px;
  color: #333333;
  border: 3px solid #333333;
  border-radius: 25px;
  margin: 1.5%;
  transition: background-color .3s, color .3s ease;
}

@media only screen and (min-width: 768px) {
  .proto-sitemap-link {
    width: 47%;
  }
}

@media only screen and (min-width: 992px) {
  .proto-sitemap-link {
    width: 30%;
  }
}

.proto-sitemap-link:hover {
  background-color: #333333;
  color: #fff;
}
